import { createSlice } from '@reduxjs/toolkit'
import { formatDate, formMonthObjectKey } from '../util/helpers'
import store from '../store'
// createSlice-funktiolla luodaan action creatorit sekä reducerit. Ei luoda uutta objektia tilaa muuttaessa, 
// sillä createSlice käyttä Immer-kirjastoa.
// Kuukauden muuttamiseen ehtolauseet, joissa testataan onko edellistä tai seuraavaa kuukautta järjestelmässä.
const viewSlice = createSlice({
  name: 'view',
  initialState: {
    timePeriod: 31,
    //month: formMonthObjectKey(formatDate(new Date()))
  },
  reducers: {
    selectTimePeriod(state, action) {
      switch (action.payload) {
      case 'month':
        state.timePeriod = 31
        break
      case 'week':
        state.timePeriod = 7
        break
      case 'day':
        state.timePeriod = 1
        break
      default:
        state.timePeriod
      }
      //state.timePeriod = action.payload
    },
    /* Setting monthObjectKey directly. It assumes the payload is a correctly formatted string, so no helper 
       functions are needed. */
    setMonth(state, action) {
      state.month = action.payload
    },
    initialize(state, action) {
      state.month = formMonthObjectKey(formatDate(new Date()))
    }
  }
})

export const { selectTimePeriod, prevMonth, nextMonth, setMonth, initialize } = viewSlice.actions
export default viewSlice.reducer