import React, { useEffect, useState } from 'react'
import Loader from './Loader'
import ChartBar from './ChartBar'
import { useLanguage } from '../hooks/useLanguage'
import { calculateAverage, standardDeviation, classifyData, getMonthName } from '../util/helpers'

const StatGrid = ({ data }) => {
  // https://www.chartjs.org/docs/latest/charts/bar.html
  const [loading, setLoading] = useState(true)
  const [averages, setAverages] = useState({})
  const [stdDevs, setStdDevs] = useState({})
  const [classifiedData, setClassifiedData] = useState({})
  const texts = useLanguage()
  
  useEffect(() => {
    const monthsInData = Object.keys(data.entries)
    let entriesInMonths = []
    monthsInData.forEach(month => {
      for (let day in data.entries[month]) {
        entriesInMonths.push(...data.entries[month][day].readings)
      }
    })

    const thisMonth = entriesInMonths.filter(m => 
      m.monthObjectKey === monthsInData[monthsInData.length - 1])
    const averages = {
      all: calculateAverage(entriesInMonths),
      currentMonth: calculateAverage(thisMonth)
    }

    const stdDevs = {
      all: standardDeviation(entriesInMonths, averages.all),
      currentMonth: standardDeviation(thisMonth, averages.currentMonth)
    }

    setStdDevs(stdDevs)    
    setAverages(averages)
    setClassifiedData({
      all: classifyData(entriesInMonths),
      currentMonth: classifyData(thisMonth),
      firstMonth: `${getMonthName(monthsInData[0], texts.lan.toLowerCase())}`,
      lastMonth: `${getMonthName(monthsInData[monthsInData.length - 1], texts.lan.toLowerCase())}`
    })
    setLoading(false)
  }, [data, texts])
  
  return (
    <div className='grid'>
      {loading ? <Loader /> :
        <>
          <div className='info grid-item'>
            <h4 className='grid-item-heading'>{classifiedData.lastMonth}</h4>
            <div className='flex-cont'>
              <div className='grid-item-data'>
                <h5 style={{ margin: '0.4em 0' }}>{texts.main.stats.avg}</h5>
                <div className='sys'>{texts.main.stats.sys}<span>{averages.currentMonth.systolic}</span></div>
                <div className='dia'>{texts.main.stats.dia}<span>{averages.currentMonth.diastolic}</span></div>
                <div className='pul'>{texts.main.stats.pul}<span>{averages.currentMonth.pulse}</span></div>
              </div>
              <div className='grid-item-data'>
                <h5 style={{ margin: '0.4em 0' }}>{texts.main.stats.dev}</h5>
                <div className='sys'>{texts.main.stats.sys}<span>{stdDevs.currentMonth.systolic}</span></div>
                <div className='dia'>{texts.main.stats.dia}<span>{stdDevs.currentMonth.diastolic}</span></div>
              </div>
              <div className='grid-item-data chart-wrap'>
                <ChartBar entries={classifiedData.currentMonth}/>
              </div>
            </div>
          </div>
          {Object.keys(data.entries).length > 1 && 
            <div className='info grid-item'>
              <h4 className='grid-item-heading'>{`${classifiedData.firstMonth} - ${classifiedData.lastMonth}`}</h4>
              <div className='flex-cont'>
                <div className='grid-item-data'>
                  <h5 style={{margin: '0.4em 0'}}>{texts.main.stats.avg}</h5>
                  <div className='sys'>{texts.main.stats.sys}<span>{averages.all.systolic}</span></div>
                  <div className='dia'>{texts.main.stats.dia}<span>{averages.all.diastolic}</span></div>
                  <div className='pul'>{texts.main.stats.pul}<span>{averages.all.pulse}</span></div>
                </div>
                <div className='grid-item-data'>
                  <h5 style={{margin: '0.4em 0'}}>{texts.main.stats.dev}</h5>
                  <div className='sys'>{texts.main.stats.sys}<span>{stdDevs.all.systolic}</span></div>
                  <div className='dia'>{texts.main.stats.dia}<span>{stdDevs.all.diastolic}</span></div>
                </div>
                <div className='grid-item-data chart-wrap'>
                  <ChartBar entries={classifiedData.all}/>
                </div>
              </div>
            </div>}
        </>}
    </div>
  )
}

export default StatGrid
